						2.mutedÁöÑÈªòËÆ§ÂÄºÊòØ falseÔºå‰ª£Ë°®ÈªòËÆ§ÊòØÁ¶ÅÈü≥ËßÜÈ¢ëÁöÑ
						3.http-cacheÈªòËÆ§ÂºÄÂêØËßÜÈ¢ëÁºìÂ≠ò
						4.posterÔºàÂ∞ÅÈù¢ÔºàÊñπÊ°à‰∏ÄÔºâÔºâÔºöËøôÈáåÁöÑÂ∞ÅÈù¢ÈªòËÆ§Â§ÑÁêÜÂ≠òÂÇ®Âú®ÈòøÈáå‰∫ëÁöÑËßÜÈ¢ë
						5.show-loadingÔºöËøôÈáåÈªòËÆ§ÂéªÊéâÊí≠ÊîæËΩ¨ÂúàÁöÑÊ†áÂøó
