<script>
	// import userList from '../new_index/data.js'//Ëøô‰∏™ÊòØÂÅáÊï∞ÊçÆ
	let audo = uni.createInnerAudioContext()
	audo.loop = true
	export default {
		data() {
			return {
				windowWidth: 0,
				windowHeight: 0,
				platform: "",
				model: "",
				deleteHeight: 0,
				dataList: [],
				k: 0,
				oldVideo: "",
				voice: "",
				timeout: "",
				current: 0,
				boxStyle:{//ËßÜÈ¢ëÔºåÂõæÁâáÂ∞ÅÈù¢Ê†∑Âºèüåüüíó
					'height': 0,
					'width': 0,
				},
				
				videoID: "",
				isShow: false,
				
				showPlay: false,//ËΩ¨ËΩÆÊòæÁ§∫ÊéßÂà∂
				rotates: 0,//ËΩ¨ËΩÆÊóãËΩ¨ËßíÂ∫¶
				rotateTime: "",//ËΩ¨ËΩÆÈÄíÂΩí‰∫ã‰ª∂ÊéßÂà∂
				xrotats: "",
				
				mpcleartime: "",
				
				isClick: false,
				
				changeTimeout: "",
				mptime: 0,
				mpstartTime: 0,
				
				duration: 500
			}
		},
		components:{
			
		},
		watch:{
			async k(k,old_k){
				// console.log(k, old_k)
				// #ifndef MP
				this.clearToTime();
				// #endif
				this.isShow = false
				this.dataList[old_k].playIng = false//Â¶ÇÊûúËßÜÈ¢ëÊöÇÂÅúÔºåÂ∞±Âä†ËΩΩÂ∞ÅÈù¢
				this.dataList[old_k].isplay = true
				this.dataList[old_k].state = 'pause'
				console.log('È¢ÑÁïôÁ¨¨' + (old_k + 1) + '‰∏™ËßÜÈ¢ëÔºö' + this.dataList[old_k]._id+''+old_k)
				// 2.0ÁâàÊú¨Â∑≤ÁªèÂéªÊéâ‰∫Ü‰∏ãÈù¢Ëøô‰∏ÄÂè•ÔºåËßÜÈ¢ë‰∏çÁî®ÊöÇÂÅúÔºåÂè™ÈúÄË¶ÅÊääÂ£∞Èü≥Á¶ÅÊ≠¢Â∞±Ë°å
				uni.createVideoContext(this.dataList[old_k]._id + '' + old_k,this).stop()//Â¶ÇÊûúËßÜÈ¢ëÊöÇÂÅúÔºåÈÇ£‰πàÊóßËßÜÈ¢ëÂÅúÊ≠¢ÔºåËøôÈáåÁöÑthis.dataList[old_k]._id + '' + old_kÔºåÂêéÈù¢Âä† old_k ÊòØ‰∏∫‰∫ÜÊØè‰∏Ä‰∏™ËßÜÈ¢ëÁöÑ id ÂÄº‰∏çÂêåÔºåËøôÊ†∑Â∞±ÂèØ‰ª•Â§ßÁ®ãÂ∫¶ÁöÑÈÅøÂÖç‰∏≤Èü≥ÈóÆÈ¢ò
				console.log('Â∑≤ÁªèÊöÇÂÅú --> Á¨¨' + (old_k + 1) + '‰∏™ËßÜÈ¢ëÔΩû')//ÊèêÁ§∫
				this.dataList[k].state = 'play'
				this.isShow = true
				this.xrotats = setTimeout(()=>{
					this.showPlay = true;
					// #ifndef MP
					this.rotateX();
					// #endif
				},200)
				// #ifdef MP
				// Â¶ÇÊûúÊòØÂ∞èÁ®ãÂ∫èÁ´Ø
				clearTimeout(this.changeTimeout);
				this.dataList[k].isplay = false
				setTimeout(()=>{
					this.dataList[k].playIng = true
				},250)
				if(this.mptime < 0.2){
					this.changeTimeout = setTimeout(()=>{
						uni.createVideoContext(this.dataList[this.k]._id+''+this.k,this).play()
					},1400)
				} else {
					uni.createVideoContext(this.dataList[this.k]._id+''+this.k,this).play()
				}
				// #endif
				// #ifdef H5
				this.dataList[k].isplay = true
				audo.src = this.dataList[k].src
				if(this.isClick){
					setTimeout(()=>{
						if (typeof WeixinJSBridge == "undefined") {
							setTimeout(()=>{
								audo.play()
								uni.createVideoContext(this.dataList[k]._id+''+k,this).seek(0)
								uni.createVideoContext(this.dataList[k]._id+''+k,this).play()
								setTimeout(()=>{
									this.dataList[k].playIng = true
								},650)
							},500)
						} else {
							WeixinJSBridge.invoke('getNetworkType', {}, e => {
								setTimeout(()=>{
									audo.play()
									uni.createVideoContext(this.dataList[k]._id+''+k,this).seek(0)
									uni.createVideoContext(this.dataList[k]._id+''+k,this).play()
									setTimeout(()=>{
										this.dataList[k].playIng = true
									},850)
								},200)
							})
						}
					},200)
				} else {
					audo.pause()
					this.dataList[k].state = 'pause'
					uni.createVideoContext(this.dataList[k]._id+''+k,this).seek(0)
					uni.createVideoContext(this.dataList[k]._id+''+k,this).pause()
				}
				// #endif
				var p = k+1;
				console.log('È¢ÑÂä†ËΩΩÁ¨¨' + (p + 1) + '‰∏™ËßÜÈ¢ëÔºö' + this.dataList[p]._id+''+p)
			}
		},
		onLoad() {
			this.platform = uni.getSystemInfoSync().platform
			this.model = uni.getSystemInfoSync().model
			var model = this.model
			if(this.platform == 'ios' && (model !== 'iPhone6' || model !== 'iPhone6s' || model !== 'iPhone7' || model !== 'iPhone8')){
				this.deleteHeight = 0//Êúâ tabbarÁöÑ ‰øÆÊîπËøôÈáåÂèØ‰ª•ÊîπÂèòËßÜÈ¢ëÈ´òÂ∫¶
			}
			this.windowWidth = uni.getSystemInfoSync().windowWidth
			this.windowHeight = uni.getSystemInfoSync().windowHeight
			this.boxStyle.width = this.windowWidth + 'px'//ÁªôÂÆΩÂ∫¶Âä†px
			this.boxStyle.height = this.windowHeight - this.deleteHeight;//Êúâ tabbarÁöÑ ‰øÆÊîπËøôÈáåÂèØ‰ª•ÊîπÂèòËßÜÈ¢ëÈ´òÂ∫¶
			this.get() //ÂàöËøõÂÖ•È°µÈù¢Âä†ËΩΩÊï∞ÊçÆ
			// #ifndef MP
			this.rotateX();
			// #endif
		},
		onShow(){
			console.log('ÂõûÂà∞ÂâçÂè∞');
			if(this.dataList.length !== 0){
				// #ifdef MP
				this.dataList[this.k].state = 'play';
				uni.createVideoContext(this.dataList[this.k]._id+''+this.k,this).play()
				// #endif
				// #ifdef H5
				if(this.isClick){
					this.dataList[this.k].state = 'play';
					uni.createVideoContext(this.dataList[this.k]._id+''+this.k,this).play()
					audo.play()
				}
				// #endif
			}
		},
		onHide(){
			// #ifdef MP
			this.dataList[this.k].state = 'pause';
			uni.createVideoContext(this.dataList[this.k]._id+''+this.k,this).pause()
			// #endif
			// #ifdef H5
			if(this.isClick){
				this.dataList[this.k].state = 'pause';
				uni.createVideoContext(this.dataList[this.k]._id+''+this.k,this).pause()
				audo.pause()
			}
			// #endif
			console.log('Âà∞ÂêéÂè∞');
		},
		methods: {
			mpTouchend(){
				this.mptime = (new Date()/1000) - this.mpstartTime;
			},
			mpTouchstart(){
				this.mpstartTime = (new Date()/1000);
			},
			dealVoice(){
				uni.showToast({
					title: 'Â§ÑÁêÜÂ£∞Èü≥',
					icon: 'none'
				})
			},
			clearToTime(){
				//Ê∏ÖÁêÜÂÆöÊó∂Âô®
				for(let i=0;i<20;i++){
					clearTimeout(this.rotateTime);
					clearTimeout(this.xrotats);
					this.showPlay = false;
					this.rotates = 0;
				}
			},
			clearTime(){
				//Ê∏ÖÁêÜÂÆöÊó∂Âô®
				for(let i=0;i<20;i++){
					clearTimeout(this.rotateTime);
					clearTimeout(this.xrotats);
				}
			},
			rotateX(){
				// clearTimeout(this.rotateTime);
				this.rotateTime = setTimeout(()=>{
					this.rotateX();
					this.showPlay = true;
					this.rotates += 1;
				},30)
			},
			closeScrollview(){
				// ÁÇπÂáªËØÑËÆ∫ÈáåÈù¢ÁöÑÂèâÂèâÔºåÂ∞±‰ºöÂÖ≥Èó≠ËØÑËÆ∫
				this.$refs.pinglun.close();
			},
			ended(){
				// 1.Êí≠ÊîæÂΩìÂâçËßÜÈ¢ëÁªìÊùüÊó∂Ëß¶ÂèëÔºåËá™Âä®ÂàáÊç¢‰∏ã‰∏Ä‰∏™ËßÜÈ¢ë
				// this.current = this.k+1
			},
			//ÁÇπÂáªÊí≠Êîæ&&ÊöÇÂÅú
			tapVideoHover(state,event){
				console.log('state--',state);
				if(state=='play'||state=='continue'){
					this.dataList[this.k].state = 'pause';
				}else{
					this.dataList[this.k].state = 'continue';
				}
				if(this.dataList[this.k].state == 'continue'){
					this.isClick = true;
					this.dataList[this.k].playIng = true
					uni.createVideoContext(this.dataList[this.k]._id+''+this.k,this).play();//ÊöÇÂÅú‰ª•ÂêéÁªßÁª≠Êí≠Êîæ
					// #ifdef MP
					this.dataList[this.k].isplay = false
					// #endif
					// #ifdef H5
					audo.play()
					// #endif
				}
				if(this.dataList[this.k].state == 'pause'){
					uni.createVideoContext(this.dataList[this.k]._id+''+this.k,this).pause();//ÊöÇÂÅú‰ª•ÂêéÁªßÁª≠Êí≠Êîæ
					// #ifdef MP
					this.dataList[this.k].isplay = true
					// #endif
					// #ifdef H5
					audo.pause()
					// #endif
				}
			},
			change(event){
				this.k = event.detail.current
			},
			animationfinish(event){
				// 1.ËøôÈáåËøõË°åÂà§Êñ≠ÔºåÂ¶ÇÊûúÊòØÊúÄÂêé‰∏Ä‰∏™ËßÜÈ¢ëÂ∞±ËøõÂÖ• get() ÊñπÊ≥ïÂä†ËΩΩËßÜÈ¢ëËøõÂÖ•ÂàóË°®
				if(this.k == this.dataList.length - 1){
					this.GET()
				}
			},
			//ÊØè‰∏ÄÁªÑÁªìÊùüÊó∂Êñ∞ÁöÑËØ∑Ê±Ç
			GET(){
				uni.request({
					url: 'https://bdb24c6d-8c19-4f80-8e7e-c9c9f037f131.bspapp.com/video',
					method: 'POST',
					data:{
						info: 'get_video'
					},
					success: (res) => {
						var msg = res.data.data
						// 2.ËøôÈáåÊääËßÜÈ¢ëÊ∑ªÂä†Âà∞ËßÜÈ¢ëÂàóË°®
						for (let i = 0; i < msg.length; i++) {
							this.dataList.push(msg[i])
						}
					}
				})
			},
			get(){
				// 1.ËøôÈáåÂºïÂÖ•ÂêéÁ´ØËØ∑Ê±ÇÊï∞ÊçÆ
				// var msg = userList
				uni.request({
					url: 'https://bdb24c6d-8c19-4f80-8e7e-c9c9f037f131.bspapp.com/video',
					method: 'POST',
					data:{
						info: 'get_video'
					},
					success: (res) => {
						this.isShow = false;
						var msg = res.data.data
						// 2.ËøôÈáåÊääËßÜÈ¢ëÊ∑ªÂä†Âà∞ËßÜÈ¢ëÂàóË°®
						for (let i = 0; i < msg.length; i++) {
							this.dataList.push(msg[i])
						}
						// 3.Êí≠ÊîæÂΩìÂâçËßÜÈ¢ë
						setTimeout(()=>{
							this.isShow = true;
							// #ifdef H5
							this.dataList[this.k].isplay = true
							// #endif
							// #ifdef MP
							// Â¶ÇÊûúÊòØÂ∞èÁ®ãÂ∫èÁ´Ø
							this.dataList[this.k].isplay = false
							this.dataList[this.k].state = 'play'
							// #endif
							this.dataList[this.k].playIng = true
							setTimeout(()=>{
								// #ifdef H5
								uni.createVideoContext(this.dataList[this.k]._id+''+this.k,this).seek(0)
								uni.createVideoContext(this.dataList[this.k]._id+''+this.k,this).pause()
								this.dataList[this.k].state = 'pause';
								audo.src = this.dataList[this.k].src;
								// #endif
								// #ifdef MP
								uni.createVideoContext(this.dataList[this.k]._id+''+this.k,this).play()
								// #endif
							},500)
						},200)
						this.videoID = this.dataList[this.k]._id
					}
				})
			},
			share(){
				uni.showToast({
					title: 'ÂàÜ‰∫´',
					icon: 'none'
				})
			},
			toComment(index){
				// Ê≥®ÊÑèÁÇπÂáªËØÑËÆ∫‰πãÂêé‰ºöÊâßË°åËøôÈáå
				/*
				Ôºà1ÔºâÂÖàÂä†ËΩΩÁºìÂÜ≤
				Ôºà2ÔºâËé∑ÂèñÂΩìÂâçËßÜÈ¢ë ID ‰ø°ÊÅØ
				Ôºà3ÔºâüåüüåüüåüüåüÈáçË¶Åüåüüåüüåüüåü
					- ‰∏ÄÂÆöË¶ÅËÆ∞ÂæóÁúã index.vue ÈáåÈù¢
					 uni.setStorageSync("user",this.peopleList[i]);
					 Ëøô‰∏™‰∏úË•øÔºåÁî®‰∫éÂ≠òÂÇ®ÂΩìÂâçÁî®Êà∑‰ø°ÊÅØ„ÄÇÂú® Êèí‰ª∂ÈáåÈù¢‰ºö‰ΩøÁî®Âà∞Ëøô‰∏™‰∏úË•øÔºå
					 ËÆ∞ÂæóÂÜô‰∏Ä‰∏ã„ÄÇ
					 
				Ôºà4ÔºâÊâìÂºÄËØÑËÆ∫
				*/ 
				uni.showToast({
					title: 'Âä†ËΩΩ‰∏≠...',
					icon: 'none',
					position: 'bottom',
					duration: 300
				})
				uni.setStorageSync("videoID",this.dataList[index]._id);
				this.videoID = this.dataList[index]._id;
				this.$refs.pinglun.open('bottom')
			},
			cLike(sss){
				this.dataList[this.k].like = !this.dataList[this.k].like
				const video = this.dataList[this.k];
				sss?video.like_n -= 1:video.like_n += 1;
			}
		}
	}
</script>

